import axios from "axios";
import React, { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import {
  Horizontal,
  HorizontalContainer,
} from "src/components/common/MyTime/styled";

import * as S from "./styled";

export const StList = () => {
  const [listOfUsers, setListOfUsers] = useState([]);
  const [searchTerm, setSearchTerm] = useState("");
  const navigate = useNavigate();
  useEffect(() => {
    if (!localStorage.getItem("accessToken")) {
      navigate("/auth/login");
      alert("      .");
    } else {
      axios
        .get("http://52.79.235.48:8080/auth", {
          //           .
          headers: { accessToken: localStorage.getItem("accessToken") },
        })
        .then((response) => {
          setListOfUsers(response.data.listOfUsers);
        });
    }
  }, []);
  return (
    <S.Container>
      <S.SearchContainer>
        <S.Search>
          <S.SearchInput
            type="text"
            placeholder=" "
            onChange={(event) => {
              setSearchTerm(event.target.value);
            }}
          />
        </S.Search>
      </S.SearchContainer>
      <HorizontalContainer>
        <Horizontal />
      </HorizontalContainer>
      <S.ListContainer>
        {listOfUsers
          .filter((value) => {
            if (searchTerm == "") {
              return value;
            } else if (
              value.user_id.toLowerCase().includes(searchTerm.toLowerCase())
            ) {
              return value;
            }
          })
          .map((value, key) => {
            return (
              <S.List key={key}>
                <S.StName to={`/profile/${value.id}`}>
                  <S.StText>{value.user_id}</S.StText>
                </S.StName>
              </S.List>
            );
          })}
      </S.ListContainer>
    </S.Container>
  );
};
